* {
  margin: 0;
}

html {
  --logo-size: 40px;
  --link-height: 60px;
  --link-color: #00acdc;
  font-family: Roboto, serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  height: var(--logo-size);
  width: var(--logo-size);
  display: block;
}

.nav__list {
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  height: var(--link-height);
  line-height: var(--link-height);
  color: #000;
  text-decoration: none;
  display: inline-block;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link:hover {
  color: var(--link-color);
}

.is-active {
  color: var(--link-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
